.instructionPageContainer {
    background-color: #f1f3f2;
    padding-bottom: 100px;
  }
  .error {
    color: red;
    font-size: 1.5rem;
    text-align: center;
    margin-top: 1rem;
    font-weight: bold;
    font-family: 'Roboto', sans-serif;
    text-transform: uppercase;
    letter-spacing: 1px;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5);
    padding: 1rem;
    border: 1px solid red;
    border-radius: 5px;
    background-color: rgba(255, 0, 0, 0.1);
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5);
    transition: all 0.3s ease;
  }
  
  .imagePara{
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      padding-top: 5vw;
      flex-wrap: wrap-reverse;
  }
  
  .image img {
    width: 50vw;
    border: 1.3vw solid;
    border-image: linear-gradient(to bottom, #d9d9d9, #757575) 1;
  }
  
  .para{
      text-align: center;
      max-width: 40vw;   
      line-height: 1.6;
  }
  .para h2 {
    color: orange;
    font-family: Shantell Sans, cursive;
    font-size: 3vw;
    font-weight: bold;
    background-color: #757575;
    margin-bottom: 1vw;
    display: inline-block;
    padding: 0vw 1vw;
  }
  
  .para p {
    line-height: 1.6;
    font-size: 1.5vw;
    font-family: Shantell Sans, cursive;
    font-weight: 600;
    background-color: #d9d9d9;
    padding: 2vw;
  }
  
  .ingredientHeading {
    margin: 10vw 10vw 2vw 10vw;
    font-size: 3vw;
    font-weight: 600;
    color: orange;
    font-family: Shantell Sans, cursive;
    text-decoration: underline;
    text-underline-offset: 5px;
  }
  .ingredientContainer {
    display: flex;
    flex-direction: row;
    gap: 1rem;
    justify-content: center;
    flex-wrap: wrap;
    padding: 20px;
  }
  .ingredient {
    display: flex;
    gap: 1rem;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    min-height: 9vw;
    min-width: 30vw;
    overflow: hidden;
    background-color: white;
  }
  
   .ingredient p{
      line-height: 1.6;
      font-size: 2vw;
      font-family: Shantell Sans, cursive;
      font-weight: 600;
      text-transform: capitalize;
   }
      
   .instructionsContainer{
      display: flex;
      flex-direction: column;
      gap: 6vw;
   }
  
  .instructionsHeading {
    font-size: 3vw;
    font-weight: 600;
    color: orange;
    font-family: Shantell Sans, cursive;
    text-decoration: underline;
    text-underline-offset: 5px;
    margin: 6vw 10vw 5vw 10vw;
  }
  
  .instruction {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 2vw;
    flex-direction: row;
  }
  .instruction h2 {
    margin-left: 5vw;
    font-size: 3vw;
    font-weight: 600;
    color: orange;
    font-family: Shantell Sans, cursive;
    background-color: #757575;
    padding: 5px 10px;
    flex-shrink: 0;
    flex-grow: 0;
  }
  .instruction p {
    line-height: 1.6;
    font-size: 1.8vw;
    font-family: Shantell Sans, cursive;
    font-weight: 600;
  }
  
  .stepIngredients {
    margin-top: 2vw;
    display: flex;
    gap: 3vw;
    align-items: flex-start;
  }
  
  .stepIngredients h3 {
    margin-left: 5vw;
    font-size: 2vw;
    font-weight: 600;
    color: black;
    font-family: Shantell Sans, cursive;
    background-color: #d9d9d9;
    padding: 5px 10px;
  }
  
  .ingredientList {
    line-height: 1.6;
    font-size: 1.8vw;
    font-family: Shantell Sans, cursive;
    font-weight: 600;
  }
  .ingredientList li {
    list-style-type: disc;
    margin-bottom: 0.25rem; /* Adds spacing between items */
  }
  
  .stepEquipment {
    margin-top: 2vw;
    display: flex;
    gap: 3vw;
    align-items: flex-start;
  }
  
  .stepEquipment h3 {
    margin-left: 5vw;
    font-size: 2vw;
    font-weight: 600;
    color: black;
    font-family: Shantell Sans, cursive;
    background-color: #d9d9d9;
    padding: 5px 10px;
  }
  
  .equipmentList {
    line-height: 1.6;
    font-size: 1.8vw;
    font-family: Shantell Sans, cursive;
    font-weight: 600;
  }
  
  .equipmententList li {
      list-style-type:disc; 
      margin-bottom: 0.25rem; /* Adds spacing between items */
    }
  
    .loaderContainer {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100vh; /* Full viewport height */
      width: 100%;   /* Full width */
      background-color: orange; /* Optional background color */
    }
  
    .loader {
      position: relative;
      width: 120px;
      height: 14px;
      border-radius: 0 0 15px 15px;
      background-color: #3e494d;
      box-shadow: 0 -1px 4px #5d6063 inset;
      animation: panex 0.5s linear alternate infinite;
      transform-origin: 170px 0;
      z-index: 10;
      perspective: 300px;
  
  }
  .loader::before {
    content: '';
    position: absolute;
    left: calc( 100% - 2px);
    top: 0;
    z-index: -2;
    height: 10px;
    width: 70px;
    border-radius: 0 4px 4px 0;
    background-repeat: no-repeat;
    background-image: linear-gradient(#6c4924, #4b2d21), linear-gradient(#4d5457 24px, transparent 0), linear-gradient(#9f9e9e 24px, transparent 0);
    background-size: 50px 10px , 4px 8px , 24px 4px;
    background-position: right center , 17px center , 0px center;
  }
    .loader::after {
      content: '';
      position: absolute;
      left: 50%;
      top: 0;
      z-index: -2;
      transform: translate(-50% , -20px) rotate3d(75, -2, 3, 78deg);
      width: 55px;
      height: 53px;
      background: #fff;
      background-image:
      radial-gradient(circle 3px , #fff6 90%, transparent 10%),
      radial-gradient(circle 12px , #ffc400 90%, transparent 10%),
      radial-gradient(circle 12px , #ffae00 100%, transparent 0);
      background-repeat: no-repeat;
      background-position: -4px -6px , -2px -2px , -1px -1px;
      box-shadow: -2px -3px #0002 inset, 0 0 4px #0003 inset;
      border-radius: 47% 36% 50% 50% / 49% 45% 42% 44%;
      animation: eggRst 1s ease-out infinite;
    }
  
  @keyframes eggRst {
    0% ,  100%{  transform: translate(-50%, -20px) rotate3d(90, 0, 0, 90deg); opacity: 0; }
    10% , 90% {  transform: translate(-50%, -30px) rotate3d(90, 0, 0, 90deg); opacity: 1; }
    25%  {transform:  translate(-50% , -40px) rotate3d(85, 17, 2, 70deg) }
    75% {transform:  translate(-50% , -40px) rotate3d(75, -3, 2, 70deg) }
    50% {transform:  translate(-55% , -50px) rotate3d(75, -8, 3, 50deg) }
  }
  @keyframes panex {
    0%{  transform: rotate(-5deg)  }
    100%{  transform: rotate(10deg)  }
  }
    
    @media(max-width:800px){
      
      .image img{
          width: 80vw;
      }
      
      .para{
          max-width: 80vw;   
      }
      .para h2{
          margin-top: 20px;
          font-size: 30px;
          margin-bottom: 10px;
          display: inline-block;
          padding: 0vw 1vw
      }
      
      .para p{
          font-size: 18px;
          padding: 10px;
          margin-bottom: 10px;
      }
      .ingredientHeading{
          margin: 50px 50px 20px 50px;
          font-size: 30px;
      }
      .ingredient{
          min-height:100px ;
          min-width: 300px;
      }
      .ingredient p{
          font-size: 20px;
       }
  
       .instructionsContainer{
          gap: 50px;
          padding: 20px;
       }
      
      
      .instructionsHeading{
          text-align: center;
         margin: 50px 50px 20px 50px;
          font-size: 30px;
      }
      
      .instruction{
          gap: 20px; 
      }
      .instruction h2{
          font-size: 30px;
      }
      .instruction p{
          font-size: 20px;
          min-width: 100px;
      }
  
      .stepIngredients{
          margin-top: 25px;
