          margin-left: 0px;
      }
      
      .ingredientList{
       font-size: 20px;
       margin-left: 20px;
      }
      .ingredientList li {
          list-style-type:disc; 
          margin-bottom: 0.25rem; /* Adds spacing between items */
        }
      
      .stepEquipment{
          margin-top: 25px;
          gap: 20px; 
      }
      
      .stepEquipment h3{
          font-size: 25px;
          margin-left: 0px;
      }
      
      .equipmentList{
          font-size: 20px;
          margin-left: 20px;
      }
      
      .equipmententList li {
          list-style-type:disc; 
          margin-bottom: 0.25rem; /* Adds spacing between items */
        }
    }
  
    @media(max-width:400px){
      .instructionsContainer{
         padding: 20px;
       }
      .instruction{
          gap: 20px; 
          flex-wrap: wrap;
      }
      .stepIngredients,.stepEquipment{
     flex-wrap: wrap;
      };    
      .stepEquipment h3,.stepIngredients h3{
          padding: 5px 8px ;
          
      }; 
    }