* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
   
    
}
.ReactLink{
text-align: center;
text-decoration: none; 
color: inherit; 
    
    
}
body{
    overflow-x: hidden;
}


.section_container {
    background-image: url(background.jpg);
    background-size: cover;
    width: 100vw; /* Full viewport width */
    /* min-height: 100vh; */
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: space-evenly; 
    padding: 5vw 0px;
  
   
}

.section_container1_category {
    display: flex;
    flex-direction: column;
    align-items: center;
   height: 30vw;
   width: 30vw;
   background-color: #d9d9d9;
   border-radius: 30px;
   cursor: pointer;
   box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
   transition: transform 0.3s ease;
   
}

.section_container1_category:hover {
    transform: scale(1.05); 
    box-shadow: 0px 0px 15px black;
}

.section_container1_category:active{
    background-color: #b0b0b0cd;
}

.section_container1_category p {
    font-family: monospace;
    font-size: 1.8vw;
    font-weight: 600;
    color: #878484;
    text-align: center; /* Centers text */
    white-space: nowrap; /* Prevents text from breaking into multiple lines */
    overflow: hidden; /* Hides any overflowed text */
    text-overflow: ellipsis; /* Adds an ellipsis (...) if text overflows */
    text-decoration: none;
}

.section_container1_category img{
    height: 25vw;
    width: 25vw;
    object-fit: cover; /* Ensures the image scales and doesn't distort */
}

.partition_line {
    width: 2px;
    height: 40vw;
    background-color: #878484; 
}


.section_container2_category {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 30vw;
    width: 30vw;
   background-color: #d9d9d9;
   border-radius: 30px;
   cursor: pointer;
   box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
   transition: transform 0.3s ease;
}
.section_container2_category:hover {
    transform: scale(1.05); 
    box-shadow: 0px 0px 15px black;
}
.section_container2_category:active{
    background-color: #b0b0b0cd;
}

.section_container2_category p{
    font-family: monospace;
    font-size: 1.8vw;
    font-weight: 600;
    color: #878484;
}
.section_container2_category img{
    width: 25vw;
   height: 25vw;
}

@media (max-width:750px){
    .section_container{
        flex-direction: column;
        gap: 50px;
        padding: 50px 0px
    }

    .section_container1_category{
        height: 300px;
        width:300px;
    }
    .section_container1_category p{
        font-size: 18px;
    }
    .section_container1_category img{
        width: 250px;
       height: 250px;
    }
    
    .section_container2_category{
        height: 300px;
        width:300px;
    }
    .section_container2_category p{
        font-size: 18px;
    }
    .section_container2_category img{
        width: 250px;
       height: 250px;
    }

    
    .partition_line {
        width: 80vw;
        height: 2px;
        background-color: #878484; 
    }
}